$font-stack: Helvetica, sans-serif;
$primary-color: #333;

.body {
    font: 100% $font-stack;
    color: $primary-color;
}

@mixin transform($property) {
    -webkit-transform: $property;
    -ms-transform: $property;
    transform: $property;
}

.box {
    @include transform(rotate(30deg));
}

%message-shared {
    border: 1px solid #ccc;
    padding: 10px;
    color: #333;
}

.message {
    @extend %message-shared;
}